// vuexÎäî reduxÏôÄ Ïú†ÏÇ¨

import Vue from "vue";
import Vuex from "vuex";
import Axios from "axios";
Vue.use(Vuex);
export let store = new Vuex.Store({
  // state...ÏÉÅÌÉúÍ∞Ä ÏïÑÎãàÎùº Í∑∏ÎÉ• dataÎùºÍ≥† ÏÉùÍ∞ÅÌï¥Ïïº Ïù¥Ìï¥Í∞Ä ÏâΩÎã§!
  // stateÎäî ÏßÅÏ†ë Í±¥Îì§ Ïàò ÏóÜÍ≥†, ÏïÑÎûòÏùò getters, mutations ÏïàÏóê Îì±Î°ùÌïú Ìï®ÏàòÎ•º ÌÜµÌï¥ÏÑúÎßå Ìï¥ÏïºÌïúÎã§!
  state: {
    name: "Johnson",
    age: 28,
    likes: 36
  },
  getters: {
    // stateÎ•º Î∞îÍπ•ÏóêÏÑú ÎÇ¥Î≥¥ÎÇ¥Î†§Î©¥, Ïó¨Í∏∞ ÏïàÏóêÏÑú Î∞òÌôò ÌîÑÎ°úÏÑ∏Ïä§Î•º Ìï®Ïàò ÌòïÏãùÏúºÎ°ú ÎßåÎì§Ïñ¥ Ï£ºÏñ¥ÏïºÌïúÎã§!
    NAME(state) {
      return state.name;
    },
    AGE(state) {
      return state.age;
    },
    LIKES(state) {
      return state.likes;
    }
  },
  mutations: {
    // 2Î≤àÏß∏ Ïù¥ÏÉÅÏùò Ïù∏ÏûêÎ•º Î∞õÏúºÎ†§Î©¥ Ìï¥Îãπ Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú commit Î©îÏÜåÎìúÎ•º Ïù¥Ïö©Ìï¥ÏïºÌïúÎã§.
    // 'ajax ÏöîÏ≤≠ÏùÑ ÏÇ¨Ïö©ÌïòÎ†§Î©¥' dispatchÎ°ú actions ÏïàÏùò Ìï®Ïàò commitÏùÑ ÌÜµÌï¥ Ìï¥ÏïºÌïúÎã§
    // ÏßÄÍ∏à Ïù¥Í±∞ Ï°¥ÎÇò Ïù¥ÏÉÅÌïòÎãà Î≥µÏäµÌïòÎ©¥ÏÑú Í≥†Ï≥êÎ≥¥Í∏∞
    SET_NAME(state, name) {
      state.name = name;
    },
    PLUS_AGE(state) {
      state.age++;
    },
    PLUS_LIKES(state) {
      state.likes++;
    }
  },
  actions: {
    // 'ajax ÏöîÏ≤≠ÏùÑ ÏÇ¨Ïö©ÌïòÎ†§Î©¥' dispatchÎ°ú actions ÏïàÏùò Ìï®Ïàò commitÏùÑ ÌÜµÌï¥ Ìï¥ÏïºÌïúÎã§
    // Ïª¥Ìè¨ÎÑåÌä∏ -> dispatch -> actions -> commit -> mutations
    // dispatch Ìï®Ïàò ÏÇ¨Ïö©
    // ÏßÄÍ∏à Ïù¥Í±∞ Ï°¥ÎÇò Ïù¥ÏÉÅÌïòÎãà Î≥µÏäµÌïòÎ©¥ÏÑú Í≥†Ï≥êÎ≥¥Í∏∞
    SET_NAME(context, data) {
      Axios.get("/more.json").then(data => {
        context.commit("SET_NAME", data);
      });
    }
  }
});
